/* nullify styles */
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/* global styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-weight: bold;
  font-size: medium;
}

/* dark theme */
html {
  --main-accent-color: #2faecb;
}

html[app-theme='dark'] {
  --app-background-color: #202938;
  --interface-background-color: #111828;

  --input-background-color: #1B2436;
  --input-border-color: #37455c;

  --login-input-background-color: #ebedff;

  --settings-points-frame-background-color: #37455c;

  --users-page-buttons-main-background-color: #37455c;
}
html[app-theme='dark'] * {
  color: whitesmoke;
}

/* light theme */
html[app-theme='light'] {
  --app-background-color: #edeef0;
  --interface-background-color: #ffffff;

  --input-background-color: #edeef0;
  --input-border-color: #d2d2d4;

  --login-input-background-color: #ebedff;

  --settings-points-frame-background-color: #edeef0;

  --users-page-buttons-main-background-color: #edeef0;
}
html[app-theme='light'] * {
  color: black;
}
